	module.exports = {"data":{"talkJson":{"description":"While Tolkien had friends who could devise ingenious ways to critique his work without sounding critical, he had others whose remarks were merciless and direct — to the point that Tolkien simply stopped sharing new chapters as he wrote The Lord of the Rings. As programmers we share many of the struggles of writers and artists, and we often react just as badly to critique of our code. From Tolkien’s experience we will draw lessons about how to make critique generous instead of damaging, and actionable instead of personal.","meta":{"duration":"PT42M41S"},"presenter":{"name":null},"slug":"how-to-shut-down-tolkien-brandon-rhodes","source":{"id":"qVlqBxpCG24","provider":"youtube"},"tag":["PyGotham"],"title":"How To Shut Down Tolkien - Brandon Rhodes"},"allTalkJson":{"edges":[{"node":{"description":"Integration of Ipython notebook and Julia","meta":{"duration":"PT26M19S"},"presenter":{"name":null},"source":{"id":"6Q1OiMsik5g","provider":"youtube"},"slug":"python-and-julia-why-do-we-need-another-language-dwight-j-browne","tag":["PyGotham"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/6Q1OiMsik5g/default.jpg","height":90,"width":120}},"title":"Python and Julia. Why do we need another language? - Dwight J. Browne"}}]}},"pathContext":{"slug":"how-to-shut-down-tolkien-brandon-rhodes","tags":["PyGotham"]}}

